<template>
  <div class="draglist"> 
    <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-rank"></i> 拖拽组件</el-breadcrumb-item>
                <el-breadcrumb-item>拖拽排序</el-breadcrumb-item>
            </el-breadcrumb>
    </div>
    <div class="container">
       <div class="plugins-tips">
                Vue.Draggable：基于 Sortable.js 的 Vue 拖拽组件。
                访问地址：<a href="https://github.com/SortableJS/Vue.Draggable" target="_blank">Vue.Draggable</a>
        </div>
        <div class="drag-box">
          <div class="drag-box-item">
            <div class="item-title">todo</div>
            <draggable v-model="todo" @remove='removeHandle' :options="dragOptions">
              <transition-group tag="div" id="todo" class="item-ul">
                <div v-for="item in todo" class="drag-list" :key="item.id">
                  {{item.content}}
                </div>
              </transition-group>
            </draggable>
          </div>
           <div class="drag-box-item">
            <div class="item-title">doing</div>
            <draggable v-model="doing" @remove='removeHandle' :options="dragOptions">
              <transition-group tag="div" id="doing" class="item-ul">
                <div v-for="item in doing" class="drag-list" :key="item.id">
                  {{item.content}}
                </div>
              </transition-group>
            </draggable>
          </div>
           <div class="drag-box-item">
            <div class="item-title">done</div>
            <draggable v-model="done" @remove='removeHandle' :options="dragOptions">
              <transition-group tag="div" id="done" class="item-ul">
                <div v-for="item in done" class="drag-list" :key="item.id">
                  {{item.content}}
                </div>
              </transition-group>
            </draggable>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
export default {
 name: 'dragList',
 data () {
   return {
     dragOptions: {
       animation: 120,
       scroll: true,
       group: 'sortlist',
       ghostClass: 'ghost-style'
     },
     todo: [
       {
         id: 1,
         content: '开发图标组件'
       },
       {
         id: 2,
         content: '开发拖拽组件'
       },
       {
         id: 3,
         content: '开发权限测试组件'
       },
       
     ],
     doing: [
       {
         id: 1,
         content: '开发登录注册页面'
       },
       {
         id: 2,
         content: '开发头部组件'
       },
       {
         id: 3,
         content: '开发表格相关组件'
       },
       {
         id: 4,
         content: '开发表单相关组件'
       }
     ],
     done: [
       {
         id: 1,
         content: '初始化项目，生成工程目录，完成相关配置'
       },
       {
         id: 2,
         content: '开发项目整体框架'
       }
     ]
   }
 },
 components: {
   draggable
 },
 methods: {
   removeHandle (event) {
     console.log(event);
     this.$message.success(`从${event.from.id} 移动到 ${event.to.id}`)
   }
 }
}
</script>

<style scoped>
  .drag-box {
    display: flex;
    user-select: none;
  }
  .drag-box-item {
    max-width: 350px;
    min-width: 350px;
    border: 0px solid #eff1f5;
    border-radius: 10px;
    background: #eff1f5;
    margin: 0 20px;
  }
  .item-title {
    padding: 10px;
    font-weight: 600;
  }
  .item-ul {
    overflow-y: scroll;
    height: 500px;
  }
  .item-ul::-webkit-scrollbar{
    width: 0;
  }
  .drag-list {
    width: 90%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border: 1px solid #fbfbfb;
    margin: 10px auto;
    border-radius: 5px;
    background: #fbfbfb;
    cursor: pointer;
    -webkit-transition: border 0.3s ease-in;
    transition: border 0.3s ease-in;
  }
  .drag-list:hover {
    border: 1px solid green;
  }
</style>